import BarcodeDetector from "barcode-detector";

// polyfill unless already supported
if (!("BarcodeDetector" in window)) {
  console.warn("[Warn] use Polyfill BardoceDetecotr. @ref : https://www.npmjs.com/package/barcode-detector");
  window.BarcodeDetector = BarcodeDetector;
}
export default {
  name: "pInputForm",
  data() {
    return {
      model: {
        text: ""
      }
    };
  },
  setup() {},
  computed: {
    userAgent() {
      return window.navigator.userAgent;
    },
    checkBrowser() {
      return "BarcodeDetector" in window.globalThis;
    }
  },
  methods: {
    console(msg) {
      this.model.text = msg;
    },
    doStart() {
      // 新しい検出器を生成
      /* var barcodeDetector = new BarcodeDetector({
        formats: ["code_39", "codabar", "ean_13"],
      }); */

      // 対応している型をチェック
      BarcodeDetector.getSupportedFormats().then(supportedFormats => {
        supportedFormats.forEach(format => this.console(format));
      });
    }
  }
};